/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */

import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import StyledDesktopTakeoverSection from './style';
import Types from './Types';

export default () => (
  <StaticQuery
    query={graphql`
      {
        file(relativePath: { eq: "desktoptakeovers/index.md" }) {
          childMarkdownRemark {
            html
            frontmatter {
              title
              imageright {
                childImageSharp {
                  fluid(maxWidth: 1280) {
                    sizes
                    srcSet
                    aspectRatio
                    base64
                    src
                    srcSetWebp
                  }
                }
              }
            }
          }
        }
      }
    `}
    render={data => (
      <StyledDesktopTakeoverSection>
        <a className="anchor-point" id="desktop-takeovers" />
        <h2>Desktop Takeovers</h2>
        <div
          className="text-wrapper"
          dangerouslySetInnerHTML={{
            __html: data.file.childMarkdownRemark.html,
          }}
        />
        <Types />
      </StyledDesktopTakeoverSection>
    )}
  />
);
